import React from 'react';
const CommentsList = ({comments}) => {
    
    const filteredComments = comments.filter((comment) => ((comment.userName || comment.username) && comment.comment) );
    return (
    <>
     <h3>Comments:</h3>
{filteredComments.map((comment,key )=> (
   
    <div className="comment" key={key}>
        <h4>{comment.username}{comment.userName}</h4>
        <p>{comment.comment}</p>

    </div>
))}
</>
)

}
export default CommentsList;